interface PerformanceMetrics {
  timestamp: number;
  operation: string;
  duration: number;
  dataSize?: number;
  memoryUsage?: number;
  storageUsage?: number;
  compressionRatio?: number;
  errors?: string[];
}

interface PerformanceBenchmark {
  operation: string;
  averageDuration: number;
  minDuration: number;
  maxDuration: number;
  totalOperations: number;
  errorRate: number;
  lastError?: string;
}

class PerformanceMonitor {
  private metrics: PerformanceMetrics[] = [];
  private maxMetricsHistory = 1000;
  private benchmarks = new Map<string, PerformanceBenchmark>();

  async startOperation(operationName: string): Promise<string> {
    const operationId = `${operationName}_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`;
    performance.mark(`${operationId}_start`);
    return operationId;
  }

  async endOperation(
    operationId: string,
    metadata?: {
      dataSize?: number;
      errors?: string[];
      compressionRatio?: number;
    },
  ): Promise<PerformanceMetrics> {
    performance.mark(`${operationId}_end`);
    performance.measure(
      operationId,
      `${operationId}_start`,
      `${operationId}_end`,
    );

    const measure = performance.getEntriesByName(
      operationId,
    )[0] as PerformanceMeasure;
    const duration = measure.duration;

    // Get memory usage if available
    let memoryUsage: number | undefined;
    if ("memory" in performance) {
      const memory = (performance as any).memory;
      memoryUsage = memory.usedJSHeapSize;
    }

    // Get storage usage
    let storageUsage: number | undefined;
    try {
      if ("storage" in navigator && "estimate" in navigator.storage) {
        const estimate = await navigator.storage.estimate();
        storageUsage = estimate.usage;
      }
    } catch (error) {
      // Storage API not available
    }

    const metric: PerformanceMetrics = {
      timestamp: Date.now(),
      operation: operationId.split("_")[0],
      duration,
      dataSize: metadata?.dataSize,
      memoryUsage,
      storageUsage,
      compressionRatio: metadata?.compressionRatio,
      errors: metadata?.errors,
    };

    this.addMetric(metric);
    this.updateBenchmark(metric);

    // Clean up performance entries
    performance.clearMarks(`${operationId}_start`);
    performance.clearMarks(`${operationId}_end`);
    performance.clearMeasures(operationId);

    return metric;
  }

  private addMetric(metric: PerformanceMetrics): void {
    this.metrics.push(metric);

    // Keep only the last N metrics to prevent memory leaks
    if (this.metrics.length > this.maxMetricsHistory) {
      this.metrics = this.metrics.slice(-this.maxMetricsHistory);
    }
  }

  private updateBenchmark(metric: PerformanceMetrics): void {
    const existing = this.benchmarks.get(metric.operation);

    if (existing) {
      const totalOps = existing.totalOperations + 1;
      const totalDuration =
        existing.averageDuration * existing.totalOperations + metric.duration;

      const updated: PerformanceBenchmark = {
        operation: metric.operation,
        averageDuration: totalDuration / totalOps,
        minDuration: Math.min(existing.minDuration, metric.duration),
        maxDuration: Math.max(existing.maxDuration, metric.duration),
        totalOperations: totalOps,
        errorRate:
          metric.errors && metric.errors.length > 0
            ? (existing.errorRate * existing.totalOperations + 1) / totalOps
            : (existing.errorRate * existing.totalOperations) / totalOps,
        lastError:
          metric.errors && metric.errors.length > 0
            ? metric.errors[0]
            : existing.lastError,
      };

      this.benchmarks.set(metric.operation, updated);
    } else {
      this.benchmarks.set(metric.operation, {
        operation: metric.operation,
        averageDuration: metric.duration,
        minDuration: metric.duration,
        maxDuration: metric.duration,
        totalOperations: 1,
        errorRate: metric.errors && metric.errors.length > 0 ? 1 : 0,
        lastError:
          metric.errors && metric.errors.length > 0
            ? metric.errors[0]
            : undefined,
      });
    }
  }

  getBenchmarks(): PerformanceBenchmark[] {
    return Array.from(this.benchmarks.values());
  }

  getMetrics(operation?: string, limit = 100): PerformanceMetrics[] {
    let filtered = this.metrics;

    if (operation) {
      filtered = this.metrics.filter((m) => m.operation === operation);
    }

    return filtered.slice(-limit);
  }

  getAverageOperationTime(operation: string): number {
    const benchmark = this.benchmarks.get(operation);
    return benchmark?.averageDuration || 0;
  }

  getSystemHealth(): {
    status: "healthy" | "warning" | "critical";
    issues: string[];
    recommendations: string[];
    metrics: {
      averageResponseTime: number;
      errorRate: number;
      memoryTrend: string;
      storageTrend: string;
    };
  } {
    const issues: string[] = [];
    const recommendations: string[] = [];
    let status: "healthy" | "warning" | "critical" = "healthy";

    // Calculate average response time across all operations
    const allBenchmarks = Array.from(this.benchmarks.values());
    const averageResponseTime =
      allBenchmarks.length > 0
        ? allBenchmarks.reduce((sum, b) => sum + b.averageDuration, 0) /
          allBenchmarks.length
        : 0;

    // Calculate overall error rate
    const totalOps = allBenchmarks.reduce(
      (sum, b) => sum + b.totalOperations,
      0,
    );
    const totalErrors = allBenchmarks.reduce(
      (sum, b) => sum + b.errorRate * b.totalOperations,
      0,
    );
    const errorRate = totalOps > 0 ? totalErrors / totalOps : 0;

    // Check for performance issues
    if (averageResponseTime > 2000) {
      status = "critical";
      issues.push(
        `Average response time is very high: ${averageResponseTime.toFixed(2)}ms`,
      );
      recommendations.push(
        "Consider optimizing data processing or using compression",
      );
    } else if (averageResponseTime > 1000) {
      status = "warning";
      issues.push(
        `Average response time is elevated: ${averageResponseTime.toFixed(2)}ms`,
      );
      recommendations.push("Monitor performance and consider optimization");
    }

    if (errorRate > 0.1) {
      status = "critical";
      issues.push(`High error rate: ${(errorRate * 100).toFixed(2)}%`);
      recommendations.push("Investigate and fix recurring errors");
    } else if (errorRate > 0.05) {
      if (status !== "critical") status = "warning";
      issues.push(`Elevated error rate: ${(errorRate * 100).toFixed(2)}%`);
    }

    // Analyze memory and storage trends
    const recentMetrics = this.metrics.slice(-50);
    const memoryTrend = this.analyzeTrend(
      recentMetrics.map((m) => m.memoryUsage).filter(Boolean) as number[],
    );
    const storageTrend = this.analyzeTrend(
      recentMetrics.map((m) => m.storageUsage).filter(Boolean) as number[],
    );

    if (memoryTrend === "increasing rapidly") {
      status = "critical";
      issues.push("Memory usage is increasing rapidly");
      recommendations.push(
        "Check for memory leaks and optimize data structures",
      );
    }

    if (storageTrend === "increasing rapidly") {
      if (status !== "critical") status = "warning";
      issues.push("Storage usage is increasing rapidly");
      recommendations.push("Consider implementing data cleanup or compression");
    }

    return {
      status,
      issues,
      recommendations,
      metrics: {
        averageResponseTime,
        errorRate,
        memoryTrend,
        storageTrend,
      },
    };
  }

  private analyzeTrend(values: number[]): string {
    if (values.length < 3) return "insufficient data";

    const firstHalf = values.slice(0, Math.floor(values.length / 2));
    const secondHalf = values.slice(Math.floor(values.length / 2));

    const firstAvg =
      firstHalf.reduce((sum, v) => sum + v, 0) / firstHalf.length;
    const secondAvg =
      secondHalf.reduce((sum, v) => sum + v, 0) / secondHalf.length;

    const changePercent = ((secondAvg - firstAvg) / firstAvg) * 100;

    if (changePercent > 20) return "increasing rapidly";
    if (changePercent > 10) return "increasing";
    if (changePercent < -20) return "decreasing rapidly";
    if (changePercent < -10) return "decreasing";
    return "stable";
  }

  async runPerformanceTest(): Promise<{
    compressionTest: { duration: number; ratio: number };
    storageTest: { duration: number; throughput: number };
    validationTest: { duration: number; itemsPerSecond: number };
  }> {
    // Test compression performance
    const compressionTest = await this.testCompression();

    // Test storage performance
    const storageTest = await this.testStorage();

    // Test validation performance
    const validationTest = await this.testValidation();

    return {
      compressionTest,
      storageTest,
      validationTest,
    };
  }

  private async testCompression(): Promise<{
    duration: number;
    ratio: number;
  }> {
    const testData = {
      agents: Array.from({ length: 100 }, (_, i) => ({
        id: `agent_${i}`,
        name: `Test Agent ${i}`,
        state: { x: Math.random() * 1000, y: Math.random() * 1000 },
        messages: Array.from({ length: 50 }, (_, j) => ({
          id: `msg_${j}`,
          content: `Test message ${j} with some content to compress`,
          timestamp: new Date().toISOString(),
        })),
      })),
    };

    const startTime = performance.now();
    const jsonString = JSON.stringify(testData);
    const encoder = new TextEncoder();
    const uint8Array = encoder.encode(jsonString);
    const duration = performance.now() - startTime;

    return {
      duration,
      ratio: jsonString.length / uint8Array.length,
    };
  }

  private async testStorage(): Promise<{
    duration: number;
    throughput: number;
  }> {
    const testItems = Array.from({ length: 100 }, (_, i) => ({
      id: `test_${i}`,
      data: `Test data item ${i}`,
      timestamp: Date.now(),
    }));

    const startTime = performance.now();

    // Simulate storage operations
    for (const item of testItems) {
      localStorage.setItem(`perf_test_${item.id}`, JSON.stringify(item));
    }

    const duration = performance.now() - startTime;

    // Cleanup
    for (const item of testItems) {
      localStorage.removeItem(`perf_test_${item.id}`);
    }

    return {
      duration,
      throughput: testItems.length / (duration / 1000), // items per second
    };
  }

  private async testValidation(): Promise<{
    duration: number;
    itemsPerSecond: number;
  }> {
    const testItems = Array.from({ length: 1000 }, (_, i) => ({
      id: `test_${i}`,
      name: `Test Item ${i}`,
      createdAt: new Date().toISOString(),
      metadata: { value: Math.random() },
    }));

    const startTime = performance.now();

    // Simulate validation
    for (const item of testItems) {
      // Simple validation
      if (!item.id || !item.name || !item.createdAt) {
        throw new Error("Validation failed");
      }
    }

    const duration = performance.now() - startTime;

    return {
      duration,
      itemsPerSecond: testItems.length / (duration / 1000),
    };
  }

  clearMetrics(): void {
    this.metrics = [];
    this.benchmarks.clear();
  }

  exportMetrics(): {
    metrics: PerformanceMetrics[];
    benchmarks: PerformanceBenchmark[];
    exportedAt: string;
  } {
    return {
      metrics: this.metrics,
      benchmarks: Array.from(this.benchmarks.values()),
      exportedAt: new Date().toISOString(),
    };
  }
}

// Singleton instance
export const performanceMonitor = new PerformanceMonitor();

// Export types
export type { PerformanceMetrics, PerformanceBenchmark };
